@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
$linkColor: #3a99d8;

.blog {
  min-height: 100%;
  // width: var(--main_width);
  width: 800px;
  margin: 24px auto 0;
  display: flex;

  &-content {
    flex: 1;
    width: calc(100% - 20rem);

    .item {
      padding: 8px;
      @include font-size-default(16);

      >a {
        color: var(--text-second-color);
      }
    }
  }

  &-sidebar {
    width: 20rem;
    margin-left: 24px;
  }
}

.pagination {
  margin: 24px 0;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .blog {
    flex-direction: column;
    width: 100%;
  }

  .blog-content {
    width: 100%;
  }

  .blog-sidebar {
    width: 100%;
    margin-left: unset;
  }

  .pagination {
    text-align: center;
  }
}